import React from "react";
import {LinkTo, Button, Welcome } from "../../../assets/Login.style";
import { Panel, Wrapper } from "../LoginPanel.style";
import SelectField from "../SelectField/SelectField";

const LoginPasswordReset = () => {
  return (
    <Wrapper>
    <Panel>
      <Welcome>Reset hasa</Welcome>
      <SelectField
        type="text"
        placeholder="Wprowad nazw"
        value="name"
        label="Wpisz nazw uytkownika:"
      />
      <p></p>
      <SelectField
        type="text"
        placeholder="Wprowad e-mail"
        value="name"
        label="Wpisz e-mail:"
      />
      <Button> Zmie Haso </Button>
      <LinkTo to="login">Powrt do panelu Logowania</LinkTo>
      <LinkTo to="contact">Skontaktuj si z nami!</LinkTo>
    </Panel>
    </Wrapper>
  );
};

export default LoginPasswordReset;

